import './App.css';
import {Routes , Route} from 'react-router-dom'
import 'react-toastify/dist/ReactToastify.css';
import React , {lazy , Suspense} from 'react';
import Loader from './Components/Loaders/Loader';

const Home = lazy(() => import ('./Components/Home/Home'))
const Hotels = lazy(() => import ('./Components/Pages/Hotels'))
const HotelDetails = lazy(() => import ('./Components/Pages/HotelDetails'))
const Login = lazy(() => import ('./Components/Pages/Login'))
const SignUp = lazy(() => import ('./Components/Pages/SignUp'))
const FvHotels = lazy(() => import ('./Components/Pages/FvHotels'))

function App() {
  return (
    <div className="App">
      <Suspense fallback={<Loader />}>

        <Routes>
            <Route path='/Hotels' element={<Home />}  />
            <Route path='/list' element={<Hotels />}   />
            <Route path='/list/:id' element={<HotelDetails />} />
            <Route path='/login' element={<Login />} />
            <Route path='/signup' element={<SignUp />} />
            <Route path='/favouriteHotels' element={<FvHotels />} />
        </Routes>        

      </Suspense>

    </div>
  );
}

export default App;
